import React, { PropTypes } from 'react';

const Cell = (props) => {
    const defaultCell = props.cells;

    const alignmentMap = {
        left: 'flex-start',
        center: 'center',
        right: 'flex-end',
    };

    const styles = {
        flexBasis: props.width ? props.width : defaultCell.width,
        alignSelf: props.alignment
            ? alignmentMap[props.alignment]
            : defaultCell.alignment,
        order: props.order,
    };

    return (
        <div style={styles}>
            {props.children}
        </div>
    );
};

Cell.propTypes = {
    width: PropTypes.number,
    alignment: PropTypes.shape({
        horizontal: PropTypes.oneOf(['left', 'center', 'right']),
        vertical: PropTypes.oneOf(['top', 'middle', 'bottom']),
    }),
    order: PropTypes.number,
    children: React.PropTypes.node,
};

export default Cell;



